import React from 'react'
import HeroSection from '../components/HomePage/HeroSection'
import HeroCarousel from '../components/HomePage/HeroCarousel'
import AiChart from '../components/HomePage/AiChart'
import FAQ from '../components/HomePage/FAQ'
import Tools from '../components/HomePage/Tools'
import ChatFeatures from '../components/HomePage/ChatFeatures'
import LiveDemo from '../components/HomePage/LiveDemo'
import HeroSectionDup from '../components/HomePage/HeroSectionDup'
import { makeStyles } from '@material-ui/core'

const useStyles = makeStyles((theme)=>({
  faqHome:{
    padding:'0 10%',
    background:theme.palette.common.black
  }
}))

function Home() {
  const classes = useStyles()
  return (
    <>
      {/* <HeroSection/> */}
      <HeroSectionDup />
      <HeroCarousel />
      <AiChart />
      <ChatFeatures />
      <LiveDemo />
      <Tools />
      <div className={classes.faqHome}>
        <FAQ />
      </div>
    </>
  )
}

export default Home