import React from 'react';
import Grid from '@material-ui/core/Grid';  // Import from "@material-ui/core" for core components
import Typography from '@material-ui/core/Typography';  // Import from "@material-ui/core"
import Robottooo from '../../assets/Robottooo.png';
import { makeStyles } from '@material-ui/core/styles';  // Use makeStyles from "@material-ui/core/styles"

const useStyles = makeStyles((theme) => ({
  aichart: {
    backgroundColor: 'var(--primarycolours-900, #0F0F16)',
    padding: '40px 10%',
    [theme.breakpoints.down("md")]: {
      padding: '20px 20px', // Adjust padding for smaller screens (mobile)
    },
  },
  
  aiChartMainText:{
    padding:'0px 30px',
    [theme.breakpoints.down("md")]: {
      padding: '0px', // Adjust padding for smaller screens (mobile and tablet)
    },
  },
  aichartHeader: {
    fontSize: '50px',
    fontWeight: 500,
    lineHeight: '70px',
    letterSpacing: '0em',
    color:'#FFFFFF',
    [theme.breakpoints.down("md")]: {
      lineHeight: '41.5px',
      fontSize: '29.65px', // Adjust font size for smaller screens (mobile and tablet)
    },
  },
  aichartText: {
    fontSize: '22px',
    fontWeight: 500,
    lineHeight: '38px',
    color:'#717280',
    paddingTop:'30px',
    paddingRight:'50px',
    [theme.breakpoints.down("md")]: {
      fontSize: '13.04px',
      lineHeight:'22.53px'
    },
  },
  aichartImage: {
    width: "100%",
    height: "100%",
    objectFit: "cover",
    [theme.breakpoints.down("md")]: {
      height: "auto",
      paddingTop:'20px'
    },
  },
}));

const AiChart = () => {
  const classes = useStyles();
  return (
    <Grid container className={classes.aichart}>
      <Grid item xs={12} sm={6} className={classes.aiChartMainText}>
        <Typography variant="p" component="div" className={classes.aichartHeader}>
          Pre-generated Ai Chart Messages
        </Typography>
        <br/>
        <Typography variant="p" className={classes.aichartText}>
          Use AI models to generate captions for each image. This can be done using models specialized in image recognition and description generation.
        </Typography>
        <br/><br/>
        <Typography variant="p" className={classes.aichartText}>Integrate an AI API into your website. Whenever an image is uploaded, the API is called, and it returns a caption.
        </Typography>
      </Grid>
      <Grid item xs={12} sm={6}>
        <img
          src={Robottooo}
          alt="AI Image"
          className={classes.aichartImage}
        />
      </Grid>
    </Grid>
  );
};

export default AiChart;