import React, { useEffect, useState } from "react";
import carousel_1 from "../../assets/carousel_1.jpg";
import carousel_2 from "../../assets/carousel_2.jpg";
import carousel_3 from "../../assets/carousel_3.jpg";
import carousel_4 from "../../assets/carousel_4.jpg";
import carousel_5 from "../../assets/carousel_5.jpg";
import { Line } from "rc-progress";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  heroCarouselRoot: {
    overflow:'hidden',
    margin: "auto",
    display: "flex",
    flexDirection: "column",
    maxWidth: "1520px",
    minHeight: "620px",
    background: "#0F0F16",
    padding:'80px 10%'
  },
  heroCarouselHeader: {
    backgroundColor: "#1F202F",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    width: "100%",
    height: "80px",
    fontSize: "30px",
    color: "white",
    fontFamily: "Raleway",
    fontWeight: "bold",
    lineHeight: "44px",
  },
  heroCarousel: {
    position: "relative",
    height: "720px",
    background: "linear-gradient(to top, rgba(0,0,0,1) 20%, rgba(0,0,0,0)) 100%",
  },
  heroCarouselImage:{
    maxWidth: "520px", /* Applied for medium (md) screens */
    height: "100%", /* Full height of the container */
    margin: "0 auto", /* Horizontally center the element */
    alignSelf: "center", /* Vertically center the element */
    transition: "all 0.4s", /* Apply a smooth transition for all properties */
    backgroundSize:'cover !important',
    "@media (min-width: 768px)": {
      maxWidth: "600px", /* Applied for large (lg) screens and above */
    },
  
    "@media (min-width: 960px)": {
      maxWidth: "800px", /* Applied for extra-large (xl) screens and above */
    }
  },
  heroCarouselItems: {
    display: "flex",
    flexDirection: "row",
    gap: "10px",
    padding: "1px",
    transition: "all 0.5s",
    marginLeft:'-10%',
    position:'absolute',
    bottom:'40px'
  },
  heroCarouselItem: {
    cursor: "pointer",
    transition: "all 0.5s",
    padding: "1px",
    height: "208px",
    width: "370px",
    backgroundColor: "#27293C",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
  },
  heroCarouselActive:{
    backgroundColor: "#FFFFFF",
  },
  heroCarouselActiveText: {
    color: "#0F0F16 !important",
  },
  
  heroCarouselText: {
    fontFamily: "Raleway",
    fontWeight: 600,
    lineHeight: "30px",
    fontSize:'24px',
    color:'#EAEAEC'
  },
  heroCarouselSubtext: {
    fontFamily: "Raleway",
    lineHeight: "relaxed",
    fontSize:'18px',
    color:'#4E5460',
    paddingTop:'20px',
    textAlign:'center'
  },
  heroCarouselProgressBar: {
    position: "absolute",
    width: "120px",
    height: "10px",
    bottom: "2%",
    left: "50%",
    transform: "translateX(-50%)",
    zIndex: "5",
  }
}));

const carouselData = [
  {
    text: "Customise Appearance",
    subtext:
      "Make your chatbot look like its part of your website with custom colours and logos.",
    bgImage: carousel_1,
    background: "linear-gradient(to top, rgba(0,0,0,1) 10%, #1D0D29 90%)",
  },
  {
    text: "Customise behavior",
    subtext:
      "Ensure that your chatbot matches your brands personality with custom instructions.",
    bgImage: carousel_2,
    background: "linear-gradient(to top, rgba(0,0,0,1) 10%, #0D2429 90%)",
  },
  {
    text: "Collect Leads",
    subtext: "Set Your Chatbot To Collect Leads to follow up with later .",
    bgImage: carousel_3,
    background: "linear-gradient(to top, rgba(0,0,0,1) 10%, #210915 90%)",
  },
  {
    text: "Get Notified",
    subtext:
      "Get daily emails with all conversations and leads collected by your chatbots.",
    bgImage: carousel_4,
    background: "linear-gradient(to top, rgba(0,0,0,1) 10%, #1D0D29 90%)",
  },
  {
    text: "Integrate With Your Tools",
    subtext:
      "Connect Your Chatbot to your favourite tools like Wordpress, Shopify, WhatsApp, Facebook Messenger",
    bgImage: carousel_5,
    background: "linear-gradient(to top, rgba(0,0,0,1) 10%, #0D2429 90%)",
  },
]

const AiChatbot = () => {
  const classes = useStyles();
  const [cover, setCover] = useState(0);

  useEffect(() => {
    const el = document.querySelector(".heroCarousels");
    if (cover >= 3) {
      el.style.transform = "translateX(-50%)";
    } else {
      el.style.transform = "translateX(0)";
    }
  }, [cover]);
  

  return (
    <div className={classes.heroCarouselRoot}>
      <div className={classes.heroCarouselHeader}>
        Everything You Need For Your Ai Chatbot
      </div>
      <div className={classes.heroCarousel} style={{ background: carouselData[cover].background }}>
        <div
          className={classes.heroCarouselImage}
          style={{
            background: `linear-gradient(to top, rgba(0,0,0,1) 20%, rgba(0,0,0,0)) 100%, url(${carouselData[cover].bgImage})`,
          }}
        />
        <div className={`${classes.heroCarouselItems} heroCarousels`}>
          {carouselData.map((item, i) => (
            <div
              key={i}
              id={i}
              className={`${classes.heroCarouselItem} ${
                cover === i ? classes.heroCarouselActive : ""
              }`}
              onClick={() => setCover(i)}
            >
              <div
                className={`${classes.heroCarouselText} ${
                  cover === i ? classes.heroCarouselActiveText : classes.heroCarouselInactiveText
                }`}
              >
                {item.text}
              </div>
              <div
                className={`${classes.heroCarouselSubtext} ${
                  cover === i ? classes.heroCarouselInactiveSubText : classes.heroCarouselActiveSubText
                }`}
              >
                {item.subtext}
              </div>
            </div>
          ))}
        </div>
        <div className={classes.heroCarouselProgressBar}>
          <Line
            className={classes.heroCarouselProgressBarLine}
            percent={20 * (cover + 1)}
            strokeWidth={4}
            strokeColor='#FE6D04'
          />
        </div>
      </div>
    </div>
  );
};

export default AiChatbot;