import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import {
  AppBar,
  Toolbar,
  Container,
  Grid,
  IconButton,
  Menu,
  MenuItem,
  Typography,
  Hidden,
  Button,
} from "@material-ui/core";  // Import from "@material-ui/core" for the core components
import { makeStyles } from '@material-ui/styles';
import MENU from "../assets/MENU.png";
import main_logo from "../assets/main_logo.png";
import notification_icon from "../assets/notification_icon.png";

import { useHistory, useLocation } from "react-router-dom/cjs/react-router-dom.min";

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: "var(--primarycolours-800, #0F0F16) !important",
    flexGrow: 1,
    minHeight: 102,
  },
  rootContainer:{
    [theme.breakpoints.down("sm")]: {
      margin:0,
      padding:0
    },
  },
  logo: {
    height: 42,
    width: 152,
    [theme.breakpoints.down("sm")]: {
      height: 30,
      width: 109,
    },
  },
  logoMain: {
    marginLeft: '5%',
    [theme.breakpoints.down('sm')]: {
      marginLeft: '0%',
    },
  },
  notificationlogo: {
    height: 54,
    width: 54,
    [theme.breakpoints.down("sm")]: {
      height: 40,
      width: 40,
    },
  },
  link: {
    color: "#8D99AE",
    fontSize: 18,
    cursor: "pointer",
    textDecoration: "none",
    zIndex:999
  },
  loginButton: {
    fontSize: '16px !important',
    borderRadius: 100,
    padding: "10px 20px !important",
    textDecoration: "none !important",
    color: "#FFFFFF !important",
    fontWeight: 500,
    lineHeight: "20px !important",
    textTransform: 'none !important',
    position: 'relative',
    zIndex: '999',
    marginRight: '20px !important'
  },
  singUpButton: {
    fontSize: '16px !important',
    borderRadius: 100,
    backgroundColor: "#05668D !important",
    padding: "10px 20px !important",
    textDecoration: "none !important",
    color: "#FFFFFF !important",
    fontWeight: 500,
    lineHeight: "20px !important",
    textTransform: 'none !important',
    position: 'relative',
    zIndex: '999'
  },
  menuIcon: {
    cursor: "pointer",
  },
  navLinksList: {
    display: "flex",
    flexGrow: 1,
    alignItems: "flex-end",
    flexDirection: "row",
    height: "100%",
    gap: "30px",
  },
  menuContainer: {
    display: "flex",
    flexDirection: "column",
    alignItems: "flex-end",
  },
  hiddenText: {
    [theme.breakpoints.down("sm")]: {
      display: "none",
    },
  },
}));

const HeaderCopy = () => {
  const classes = useStyles();
  const history = useHistory()

  function HeaderWithConditionalRendering() {
    const location = useLocation();
    const isLogin = location.pathname === '/login';
    const isSignup = location.pathname === '/signup';
    const isMyProfile = location.pathname === '/my-profile'

    const loginBtn = (
      <Button className={classes.loginButton} onClick={handleLoginClick}>
        Log in
      </Button>
    );

    const signUpBtn = (
      <Button className={classes.singUpButton} onClick={handleSignUpClick}>
        Sign up
      </Button>
    );

    return (
      <>
        {isLogin && signUpBtn}
        {isSignup && loginBtn}
        {!isLogin && !isSignup && !isMyProfile && (
          <>
            {loginBtn}
            {signUpBtn}
          </>
        )}
      </>
    );
  }

    const NAV_LINKS = [
      { key: "my_chatbot", label: "My Chatbot", path:'/my-chatbot'},
      { key: "preview", label: "Preview", path:'/preview' },
      { key: "invite", label: "Invite" , path:'/invite'},
      { key: "pricing", label: "Pricing" , path:'/pricing'},
      { key: "integrates", label: "Integrates", path:'/integrates' },
    ];

  // const NAV_LINKS = []

  const [isMenuOpen, setIsMenuOpen] = useState(null);

  const toggleMenu = (event) => {
    setIsMenuOpen(event.currentTarget);
  };

  const closeMenu = () => {
    setIsMenuOpen(null);
  };

  const handleLoginClick = () => {
    history.push('/login')
  }

  const handleSignUpClick = () => {
    history.push('/signup')
  }

  return (
    <AppBar position="static" className={classes.root}>
      <Container maxWidth={false} className={classes.rootContainer}>
        <Toolbar>
          <Grid
            container
            justifyContent="space-between"
            alignItems="center"
            style={{ minHeight: "102px" }}
          >
            <Grid item alignItems="center" className={classes.logoMain}>
              <NavLink to="/" className={classes.link}>
                <img src={main_logo} alt="logo" className={classes.logo} />
              </NavLink>
            </Grid>
            <Grid item>
              <Grid container spacing={2} alignItems="center">
                <Grid item>
                  <Hidden mdDown>
                    <ul className={classes.navLinksList}>
                      {NAV_LINKS.map((link) => (
                        <NavLink to={link.path} key={link.key} className={classes.link}>
                          {link.label}
                        </NavLink>
                      ))}
                    </ul>
                  </Hidden>
                  <Hidden mdUp>
                    {/* <IconButton
                      className={classes.menuIcon}
                      onClick={toggleMenu}
                    >
                      <img src={MENU} alt="menu" height={40} width={40} />
                    </IconButton> */}
                    <Menu
                      anchorEl={isMenuOpen}
                      open={Boolean(isMenuOpen)}
                      onClose={closeMenu}
                      className={classes.menuContainer}
                    >
                      {NAV_LINKS.map((link) => (
                        <MenuItem key={link.key} onClick={closeMenu}>
                          <NavLink to={"/about"} className={classes.link}>
                            {link.label}
                          </NavLink>
                        </MenuItem>
                      ))}
                    </Menu>
                  </Hidden>
                </Grid>
              </Grid>
            </Grid>
            <Grid item style={{ marginRight: "5%" }}>
              <Grid container spacing={2} alignItems="center">
                {/* <Grid item>
                  <img
                    src={notification_icon}
                    className={classes.notificationlogo}
                    alt="notification"
                  />
                </Grid> */}
                <Grid item className={classes.hiddenText}>
                  <Typography variant="body1">
                    <Hidden smUp>
                      {NAV_LINKS.map((link) => (
                        <NavLink to={"/about"} key={link.key} className={classes.link}>
                          {link.label}
                        </NavLink>
                      ))}
                    </Hidden>
                  </Typography>
                </Grid>
                <Hidden smDown>
                  <Grid item>
                    <HeaderWithConditionalRendering />
                  </Grid>
                </Hidden>
              </Grid>
            </Grid>
          </Grid>
        </Toolbar>
      </Container>
    </AppBar>
  );
};

export default HeaderCopy;