import React from 'react';
import { Grid, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import { rwl, mobile,featuresIcon, Loop4} from '../../assets';

const useStyles = makeStyles((theme) => ({
  chatContainer: {
    backgroundColor: 'var(--primarycolours-900, #0F0F16) !important',
    padding: '20px 5%',
    position: 'relative',
    paddingBottom:'100px'
  },
  imgContainer: {
    position: 'absolute',
    top: '-100px',
    width: '250px',
    height: '250px',
    [theme.breakpoints.up('4xl')]: {
      left: '420px',
      width: '460px',
      height: '460px',
    },
  },
  img: {
    width: 'auto',
    height: 'auto',
    [theme.breakpoints.down('sm')]: {
    width: '335px',
      height: '477px',
    },
  },
  infoContainer: {
    display: 'flex',
    alignItems: 'center',
    [theme.breakpoints.down('sm')]: {
      flexDirection: 'column-reverse !important',
      alignItems: 'center',
    },
  },
  infoList: {
    marginLeft: '28px',
    display: 'flex',
    flexDirection: 'column',
    gap: '25px',
    paddingRight: "6%",
    [theme.breakpoints.down('sm')]: {
      marginLeft: '0'
    },
  },
  listItem: {
    color: '#FFFFFF',
    listStyle: 'none',
    fontFamily: '500',
    fontSize: '34px',
    [theme.breakpoints.up('4xl')]: {
      fontSize: '3xl',
    },
    lineHeight: '32px',
    [theme.breakpoints.down('sm')]: {
      fontSize: '21px',
      lineHeight: '19.8px',
    },
  },
  description: {
    fontFamily: '500',
    fontSize: '22px',
    marginLeft: '40px',
    [theme.breakpoints.up('4xl')]: {
      fontSize: '20px',
    },
    lineHeight: '38px',
    color: '#717280',
    [theme.breakpoints.down('sm')]: {
      marginLeft: '0',
      fontSize: '13px',
      lineHeight: '23px',
    },
  },
  featuresICON:{
    marginRight:'10px'
  },
  mobileText:{
    position:'absolute',
    fontSize:'32px',
    lineHeight:'32px',
    color:'#FFFFFF',
    left:'10%',
    bottom:'18%'
  },
  featuresLoop4:{
    marginLeft:'-30%',
    marginBottom:'-2%'
  }
}));

const ChatFeatures = () => {
  const classes = useStyles();

  return (
    <Grid container className={classes.chatContainer}>
      <Grid item xs={12}>
        <div className={classes.imgContainer}>
          <img src={rwl} alt="RWL Logo" className={classes.img} />
        </div>
      </Grid>
      <Grid item xs={12} className={classes.infoContainer}>
        <img src={mobile} alt="Mobile Image" className={classes.img}/>
        <div className={classes.mobileText}>
            Get Started with<br/><span style={{fontSize:"70px",lineHeight:'54px'}}> Urbanchat.ai <img src={Loop4} width={195} height={95} className={classes.featuresLoop4}/></span><br/><br/> in 3 Easy Steps
        </div>
        <div className={classes.infoList}>
          <li className={classes.listItem}><img src={featuresIcon} height={22} width={22} className={classes.featuresICON}/>Create Your Chatbot</li>
          <p className={classes.description}>
            Upload your documents in PDF or DOC format. Provide website URLs for
            Urbanchat.ai to crawl and gather information.
          </p>
          <li className={classes.listItem}><img src={featuresIcon} height={22} width={22} className={classes.featuresICON}/>Customize and Set Preferences</li>
          <p className={classes.description}>
            Adjust settings like tone, politeness, selling intensity, and
            directness. Preview your chatbot's responses to ensure they match your
            brand's voice.
          </p>
          <li className={classes.listItem}><img src={featuresIcon} height={22} width={22} className={classes.featuresICON}/>Integrate with Your Desired Platform</li>
          <p className={classes.description}>
            Choose from Facebook Messenger, WhatsApp, WordPress, Shopify, and more.
            Just a few clicks and your AI chatbot is live, enhancing your customer
            engagement!
          </p>
        </div>
      </Grid>
    </Grid>
  );
};

export default ChatFeatures;
