import React from 'react';
import { Container, Grid, Typography, Link } from "@material-ui/core";
import main_logo from '../assets/main_logo.png';
import twitter_logo from '../assets/twitter_logo.png';
import linkedin_logo from '../assets/linkedin_logo.png';
import facebook_logo from '../assets/facebook_logo.png';
import instagram_logo from '../assets/instagram_logo.png';
import { makeStyles } from '@material-ui/styles';
import { useHistory} from "react-router-dom/cjs/react-router-dom.min";

const flexbox = {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
};

const useStyles = makeStyles((theme) => ({
    footer: {
        backgroundColor: '#181924',
        paddingTop: '30px',
    },
    footerContainer: {
        ...flexbox,
        marginTop: '40px',
        marginBottom: '20px'
    },
    footerBottomContainer: {
        ...flexbox,
        backgroundColor:'var(--primarycolours-900, #0F0F16)',
        height: '100px',
        fontSize: '18px',
        fontWeight: 500,
        lineHeight: '24px' /* 133.333% */
    },
    socialIcons: {
        display: 'flex',
        flexGrow: 1,
        alignItems: 'flex-end',
        flexDirection: 'row',
        gap: '30px',
        cursor:'pointer'
    },
    links: {
        color: '#8D99AE',
        fontSize: 20,
        fontWeight: 600,
        display: 'flex',
        flexGrow: 1,
        alignItems: 'flex-end',
        flexDirection: 'row',
        gap: '60px',
        [theme.breakpoints.up('sm')]: {
            flexDirection: 'row',
        },
        [theme.breakpoints.down('md')]: {
            gap:'30px',
        },
        [theme.breakpoints.down('sm')]: {
            fontSize: '8px !important',
            justifyContent:'center',
            gap:'30px',
        },
        justifyContent: 'space-between',
    },
    link: {
        margin: '0 10px',
        textDecoration: 'none !important',
        textTransform: 'uppercase',
        cursor: 'pointer',
        color: '#8D99AE !important'
    },
    copyright: {
        marginTop: 10,
        color: 'white',
        fontSize: 16,
        [theme.breakpoints.down('sm')]: {
            fontSize: '12px !imporatant',
        },
    },
    footerLogo: {
        height: 68,
        width: 247,
        [theme.breakpoints.down('sm')]: {
            height: '30px',
            width: '109px',
        },
    },
    footerSocialIcons:{
        [theme.breakpoints.down('sm')]: {
            height: '18px',
            width: '18px',
        },
    }
}));

const Footer = () => {
    const classes = useStyles();
    const history = useHistory()
    const socialIcons = [
        { src: twitter_logo, alt: 'Twitter' },
        { src: instagram_logo, alt: 'Instagram' },
        { src: facebook_logo, alt: 'Facebook' },
        { src: linkedin_logo, alt: 'LinkedIn' },
    ];

    const links = [
        {text:'Terms And Conditions',link:'/terms-of-service'},
        {text:'Cache and Cookie',link:'#'},
        {text:'Privacy policy',link:'/privacy-policy'},
        {text:'Contact Us',link:'#'},
    ];

    return (
        <footer className={classes.footer}>
            <Container maxWidth={false}>
                <Grid container className={classes.footerContainer}>
                    <Grid item >
                        <img src={main_logo} alt="logo" className={classes.footerLogo} />
                    </Grid>
                </Grid>
                <Grid container className={classes.footerContainer}>
                    <Grid item>
                        <Typography className={classes.links}>
                            {links.map((link, index) => (
                                <Link key={index}  onClick={()=>history.push(link.link)} className={classes.link}>
                                    {link.text}
                                </Link>
                            ))}
                        </Typography>
                    </Grid>
                </Grid>
                <Grid container className={classes.footerContainer}>
                    <Grid item>
                        <div className={classes.socialIcons}>
                            {socialIcons.map((icon, index) => (
                                <img key={index} src={icon.src} alt={icon.alt} height={30} width={26} className={classes.footerSocialIcons}/>
                            ))}
                        </div>
                    </Grid>
                </Grid>
            </Container>
            <Grid container className={classes.footerBottomContainer}>
                <Grid item>
                    <Typography className={classes.copyright} align="center">
                        Copyright  2010-2023 Urbanchat Company. All rights reserved.
                    </Typography>
                    <Typography className={classes.copyright} style={{color:'#8D99AE',marginTop:'10px'}} align="center">
                        Version 2.4
                    </Typography>
                </Grid>
            </Grid>

        </footer>
    );
};

export default Footer;