import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';
import {
  AppBar,
  Toolbar,
  Container,
  Grid,
  IconButton,
  Menu,
  MenuItem,
  Typography,
  Hidden,
} from '@material-ui/core';  // Import from "@material-ui/core" for the core components
import { makeStyles } from '@material-ui/styles';  // Use makeStyles from "@material-ui/styles"
import MENU from '../assets/MENU.png';
import main_logo from '../assets/main_logo.png';
import notification_icon from '../assets/notification_icon.png';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: "var(--primarycolours-800, #181924) !important",
    flexGrow: 1,
    minHeight: 102,
  },
  logo: {
    height: 42,
    width: 152,
    [theme.breakpoints.down("sm")]: {
      height: 30,
      width: 109,
    },
  },
  notificationlogo: {
    height: 54,
    width: 54,
    [theme.breakpoints.down("sm")]: {
      height: 40,
      width: 40,
    },
  },
  link: {
    color: "#8D99AE",
    fontSize: 18,
    cursor: "pointer",
    textDecoration: "none",
  },
  loginButton: {
    fontSize: 18,
    backgroundColor: "#05668D",
    borderRadius: 100,
    padding: "10px 20px",
    textDecoration: "none",
    color: "#fff",
    fontWeight: 600,
    lineHeight: "20px",
  },
  menuIcon: {
    cursor: "pointer",
  },
  navLinksList: {
    display: "flex",
    flexGrow: 1,
    alignItems: "flex-end",
    flexDirection: "row",
    height: "100%",
    gap: "30px",
  },
  menuContainer: {
    display: "flex",
    flexDirection: "column",
    alignItems: "flex-end",
  },
  hiddenText: {
    [theme.breakpoints.down("sm")]: {
      display: "none",
    },
  },
}));

const Header = () => {
  const classes = useStyles();
  const NAV_LINKS = [
    { key: "my_chatbot", label: "My Chatbot" },
    { key: "preview", label: "Preview" },
    { key: "invite", label: "Invite" },
    { key: "pricing", label: "Pricing" },
    { key: "integrates", label: "Integrates" },
  ];

  const [isMenuOpen, setIsMenuOpen] = useState(null);

  const toggleMenu = (event) => {
    setIsMenuOpen(event.currentTarget);
  };

  const closeMenu = () => {
    setIsMenuOpen(null);
  };

  return (
    <AppBar position="fixed" className={classes.root}>
      <Container maxWidth={false}>
        <Toolbar>
          <Grid
            container
            justifyContent="space-between"
            alignItems="center"
            style={{ minHeight: "102px" }}
          >
            <Grid item alignItems="center" style={{ marginLeft: "5%" }}>
              <NavLink to="/" className={classes.link}>
                <img src={main_logo} alt="logo" className={classes.logo} />
              </NavLink>
            </Grid>
            <Grid item>
              <Grid container spacing={2} alignItems="center">
                <Grid item>
                  <Hidden mdDown>
                    <ul className={classes.navLinksList}>
                      {NAV_LINKS.map((link) => (
                        <NavLink to={"/about"} key={link.key} className={classes.link}>
                          {link.label}
                        </NavLink>
                      ))}
                    </ul>
                  </Hidden>
                  <Hidden mdUp>
                    <IconButton
                      className={classes.menuIcon}
                      onClick={toggleMenu}
                    >
                      <img src={MENU} alt="menu" height={40} width={40} />
                    </IconButton>
                    <Menu
                      anchorEl={isMenuOpen}
                      open={Boolean(isMenuOpen)}
                      onClose={closeMenu}
                      className={classes.menuContainer}
                    >
                      {NAV_LINKS.map((link) => (
                        <MenuItem key={link.key} onClick={closeMenu}>
                          <NavLink to={"/about"} className={classes.link}>
                            {link.label}
                          </NavLink>
                        </MenuItem>
                      ))}
                    </Menu>
                  </Hidden>
                </Grid>
              </Grid>
            </Grid>
            <Grid item style={{ marginRight: "5%" }}>
              <Grid container spacing={2} alignItems="center">
                <Grid item>
                  <img
                    src={notification_icon}
                    className={classes.notificationlogo}
                    alt="notification"
                  />
                </Grid>
                <Grid item className={classes.hiddenText}>
                  <Typography variant="body1">
                    <Hidden smUp>
                      {NAV_LINKS.map((link) => (
                        <NavLink to={"/about"} key={link.key} className={classes.link}>
                          {link.label}
                        </NavLink>
                      ))}
                    </Hidden>
                  </Typography>
                </Grid>
                <Hidden smDown>
                  <Grid item>
                    <NavLink to="/login" className={classes.loginButton}>
                      Login/SignUp
                    </NavLink>
                  </Grid>
                </Hidden>
              </Grid>
            </Grid>
          </Grid>
        </Toolbar>
      </Container>
    </AppBar>
  );
};

export default Header;