import React from 'react';
import { makeStyles } from '@material-ui/core/styles';  // Use makeStyles from "@material-ui/core/styles"
import Grid from '@material-ui/core/Grid';  // Import from "@material-ui/core" for core components
import Container from '@material-ui/core/Container';  // Import from "@material-ui/core"
import Typography from '@material-ui/core/Typography';  // Import from "@material-ui/core"
import Button from '@material-ui/core/Button';  // Import from "@material-ui/core"

import wordpressLogo from '../../assets/wordpress_logo.png';
import shopifyLogo from '../../assets/shopify_logo.png';
import whatsappLogo from '../../assets/whatsapp_logo.png';
import facebookMessengerLogo from '../../assets/facebook_messenger.png';
import robotImage from '../../assets/robot.png';
import underlineImage from '../../assets/Underline9.png';

import { ReactSVG } from 'react-svg';
import blobImage from '../../assets/Blob/Blob_10.svg';
import vectorImage from '../../assets/Vector/Vector_3.svg';
import { useHistory } from 'react-router-dom';  // Import "useHistory" directly from "react-router-dom"

const useStyles = makeStyles((theme) => ({
  heroSection: {
    backgroundColor: 'var(--primarycolours-900, #0F0F16)',
    padding: '20px',
    [theme.breakpoints.up('sm')]: {
      padding: '40px',
    },
    [theme.breakpoints.up('md')]: {
      padding: '60px 7.5%',
    },
    position: 'relative',
  },
  robotImageContainer:{
    [theme.breakpoints.up('sm')]: {
      marginRight:'0px'
      },
  },
  robotImage: {
    position: 'absolute',
    right: '0px',
    width: '800px',
    top: '-120px',
    [theme.breakpoints.down('xs')]: {
      width: '300px',
      top: '100px',
    },
  },
  blobImage: {
    position: 'absolute',
    top: '50px',
    left: '300px',
    width: '93px',
    height: '223px',
    [theme.breakpoints.up('xl')]: {
      top: '80px',
      left: '390px',
    },
    [theme.breakpoints.up('lg')]: {
      top: '120px',
      left: '29%',
    },
    [theme.breakpoints.down('sm')]: {
      right:'50px',
      top: '80px',
      width:'47px !important',
      height:'113px !important'
    },
  },
  vectorImage: {
    position: 'absolute',
    top: '0px',
    right: '70px',
    transform: 'rotate(-3.58deg)',
    [theme.breakpoints.up('lg')]: {
      top: '-80px',
    },
    [theme.breakpoints.down('md')]: {
      // Adjust as needed
    },
    [theme.breakpoints.down('sm')]: {
      // Adjust as needed
    },
    [theme.breakpoints.down('xs')]: {
      // Adjust as needed
    },
  },
  mainTitle: {
    color: '#FFF',
    fontSize: '96px !important',
    fontWeight: 900,
    lineHeight: '82px',
    position: 'relative',
    zIndex: 100,
    // [theme.breakpoints.up('lg')]: {
    //   fontSize: '70px',
    //   fontWeight: 700,
    // },
    [theme.breakpoints.up('xl')]: {
      fontSize: '70px',
    },
    [theme.breakpoints.down('sm')]: {
      fontSize: '36px',
    },
    fontFamily: 'Raleway Bolder',
  },
  underlineImage: {
    width: '20px',
    verticalAlign: 'top',
    position: 'absolute',
    top: '20px',
    left: '0px',
    marginBottom: '20px',
    [theme.breakpoints.down('sm')]: {
      top:'10px'
    },
  },
  underlineImageIm:{
    [theme.breakpoints.down('sm')]: {
      width: '181px',
      height:'3.56px'
    },
  },
  description: {
    fontSize: '16px',
    color: '#FFFFFF',
    lineHeight: '28px',
    marginTop: '30px',
    [theme.breakpoints.up('xl')]: {
      fontSize: '22px',
    },
    [theme.breakpoints.up('lg')]: {
      maxWidth: '320px',
    },
    [theme.breakpoints.up('md')]: {
      maxWidth: '250px',
    },
    [theme.breakpoints.down('xs')]: {
      maxWidth: '300px',
    },
  },
  buildButton: {
    width: '258px',
    height: '64px',
    marginTop: '20px',
    padding: '18px 26px',
    borderRadius: '14px !important',
    gap: '10px',
    background: 'linear-gradient(105.19deg, #AA07E0 36%, #F80282 90%, #FD309B 100%)',
    color: '#FFFFFF !important'
    // Add any other styles or hover effects as needed
  },
  freeTrial: {
    fontSize: '18px',
    fontWeight: 700,
    color: '#717280',
    lineHeight: 'tight',
  },
  integrations: {
    fontSize: '1rem',
    fontWeight: 'bold',
    textTransform: 'uppercase',
    color: '#888',
    marginTop: '50px',
    [theme.breakpoints.up('sm')]: {
      flexDirection: 'row',
    },
    paddingTop: '30px'
  },
  integrationItem: {
    display: 'flex',
    alignItems: 'center',
    gap: '4px',
    paddingTop: '20px'
  },
  logoImage: {
    width: '24px',
    height: '24px',
  },
}));

const HeroSectionDup = () => {
  const classes = useStyles();
  const history = useHistory();
  const handlePush = (path) => {
    history.push(path);
  }
  return (
    <div className={classes.heroSection}>
      <Container maxWidth='xl'>
        <Grid container spacing={2} className={classes.heroContainer}>
          <Grid item xs={12} md={8}>
            <Typography variant="p" className={classes.mainTitle}>
              The Ultimate
              <span className={classes.underlineImage}>
                <img src={underlineImage} alt="Underline" className={classes.underlineImageIm} />
              </span>
              <br />
              Ai Chatbot
              <br /> 
              <span style={{color:'#AA07E0'}}>Solution</span>
              <br />
            </Typography>
            <Typography variant="p" className={classes.description}>
              <br />
              Simply link your data sources and experience an AI chat interface tailored<br/> to outshine. Connect it effortlessly to platforms like WhatsApp Business,<br/> Facebook, Shopify, or your own website. Drive more bookings, orders, and leads instantly.
            </Typography>
            <br /><br /><br />
            <Button className={classes.buildButton} onClick={()=>handlePush('/login')}>Join waiting list</Button><br/><br/>
            <span className={classes.freeTrial}>
            Join the waiting list and unlock a 3-month free trial!
            </span>
            {/* <Grid container spacing={2} className={classes.integrations}>
              <Grid item xs={12}>
              <Typography variant="body1">UrbanChat integrates</Typography>
              </Grid>
              <Grid item xs={4}>
              <div className={classes.integrationItem}>
                <img src={wordpressLogo} className={classes.logoImage} alt="WordPress Logo" />
                <Typography variant="body1">WordPress</Typography>
              </div>
              </Grid>
              <Grid item xs={4}>
              <div className={classes.integrationItem}>
                <img src={shopifyLogo} className={classes.logoImage} alt="Shopify Logo" />
                <Typography variant="body1">Shopify</Typography>
              </div>
              </Grid>
              <Grid item xs={4}>
              <div className={classes.integrationItem}>
                <img src={whatsappLogo} className={classes.logoImage} alt="WhatsApp Logo" />
                <Typography variant="body1">WhatsApp</Typography>
              </div>
              </Grid>
              <Grid item xs={4}>
              <div className={classes.integrationItem}>
                <img src={facebookMessengerLogo} className={classes.logoImage} alt="Facebook Messenger Logo" />
                <Typography variant="body1">Facebook Messenger</Typography>
              </div>
              </Grid>
            </Grid> */}
          </Grid>
          <Grid item xs={12} md={4} className={classes.robotImageContainer}>
            <img src={robotImage} className={classes.robotImage} alt="Robot" />
          </Grid>
        </Grid>
      </Container>
      {/* <ReactSVG src={blobImage} className={classes.blobImage} /> */}
      <ReactSVG src={vectorImage} className={classes.vectorImage} />
    </div>
  );
};

export default HeroSectionDup;