import React, { useState } from 'react';
import { Container, Typography, Grid, TextField, Button, List, ListItem, ListItemText } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import Ellipse from '../../assets/Ellipse.png';
import EllipseInner from '../../assets/EllipseInner.png'
import Chatdemo from './ChatDemo';

const useStyles = makeStyles((theme) => ({
    liveDemo: {
        backgroundColor: 'var(--primarycolours-900, #0F0F16)',
    },
    livemainDemo: {
        padding: '0 10%'
    },
    livemainDemoHeading: {
        color: '#FFFFFF',
        fontFamily: 'Raleway',
        fontSize: '60px',
        fontWeight: 500,
        lineHeight: '64px',
        position: 'relative',
        left:'35%'
    },
    imageLayer1: {
        position: 'absolute',
        top: 5,
        left: 0,
        width: '54px',
        height: '54px',
        objectFit: 'cover',
    },
    imageLayer2: {
        position: 'absolute',
        top: '25%',
        left: '3.5%',
        width: '28px',
        height: '28px',
        objectFit: 'cover',
    },
    livePrimaryText: {
        fontSize: '22px',
        fontWeight: 500,
        lineHeight: '38px',
        color: '#717280',
        textAlign:'center',
        padding: "0 10%"
    }
}))

function LiveDemo() {
    const classes = useStyles()
    const [input, setInput] = useState('');
    const [messages, setMessages] = useState([
        { text: 'Hello! How can I help you?', isUser: false },
        // Add more default messages here
    ]);

    const handleSendMessage = () => {
        if (input.trim() === '') return;

        setMessages([...messages, { text: input, isUser: true }]);
        setInput('');

        // Simulate a response from the chatbot (you may replace this with actual chatbot logic)
        setTimeout(() => {
            setMessages([...messages, { text: 'I received your message: ' + input, isUser: false }]);
        }, 1000);
    };

    return (
        <Container maxWidth="false" className={classes.liveDemo}>
            <div className={classes.livemainDemo}>
                <Typography variant="p" align="center" className={classes.livemainDemoHeading}>
                    <img src={Ellipse} alt="Image 1" width={54} height={54} className={classes.imageLayer1} />
                    <img src={EllipseInner} alt="Image 2" width={28} height={28} className={classes.imageLayer2} />
                    <span style={{ paddingLeft: '70px' }}>Live Demo</span>
                </Typography>
                <br/><br/>
                <p className={classes.livePrimaryText}>
                    This chatbot was trained on a document explaining UrbanChat,<span style={{color:'#EAEAEC'}}>UrbanChat</span> You can embed a widget like this on any page on your website....!
                </p>
                <br/><br/>
                <Chatdemo/>
            </div>
        </Container>
    );
}

export default LiveDemo