import React from "react";
import "../../styles/CreateChatbot/common.css";
import "../../styles/CreateChatbot/extra-tab-common.css";
import logo from "../../assets/logo.png";
import copyIcon from "../../assets/Icons/Copy.png";

const code = `
<script>
window.embeddedChatbotConfig = {
chatbotId: "kJirqYsi1RIDRMrXRg7kp",
domain: "www.chatbase.co"
}
</script>
<script
src="https://www.chatbase.co/embed.min.js"
chatbotId="kJirqYsi1RIDRMrXRg7kp"
domain="www.chatbase.co"
defer>
</script>
`;

export default function EmbedCode() {
  return (
    <div className="main-content-holder embed-code">
      <div className="header">
        <img src={logo} alt="logo" />
      </div>

      <div className="content">
        <div className="code-area">
          <div>{code}</div>
          <button className="hover responsive-hide">
            <div>
              <img src={copyIcon} />
            </div>
              Copy Code
          </button>
        </div>

        <button className="hover btn-outside">
            <div>
              <img src={copyIcon} />
            </div>
              Copy Code
          </button>
      </div>
    </div>
  );
}
